<template>
  <section class="management-report">
    <!-- title -->
    <div class="text-center mb-2">
      <div class="d-flex align-items-center justify-content-center mb-2">
        <b-avatar class="mr-1" size="40" variant="light-danger">
          <img :src="require('@/assets/icons/building_icon.svg')" alt="" draggable="false" width="30" />
        </b-avatar>
        <h4 class="m-0">گزارشات مدیریتی</h4>
      </div>
      <span>برای دریافت گزارشات مدیریتی لطفا بر روی نام گزارش کلیک کنید</span>
    </div>

    <!-- get report item -->
    <main>
      <!-- item one -->
      <b-card>
        <div @click="goReport(1)" class="cursor-pointer">
          <div class="d-flex align-items-center">
            <!-- icon -->
            <b-avatar size="28" variant="light-success">
              <img :src="require('@/assets/icons/other-type.svg')" alt="" />
            </b-avatar>

            <!-- text -->
            <p class="m-0">گزارش بدهی واحدها بر اساس سر فصل</p>
          </div>
        </div>
      </b-card>

      <!-- item two -->
      <b-card>
        <div @click="goReport(2)" class="cursor-pointer">
          <div class="d-flex align-items-center">
            <!-- icon -->
            <b-avatar size="28" variant="light-success">
              <img :src="require('@/assets/icons/other-type.svg')" alt="" />
            </b-avatar>

            <!-- text -->
            <p class="m-0">گزارش تجمیعی پرداختی ها</p>
          </div>
        </div>
      </b-card>

      <!-- item three -->
      <b-card>
        <div @click="goReport(3)" class="cursor-pointer">
          <div class="d-flex align-items-center">
            <!-- icon -->
            <b-avatar size="28" variant="light-primary">
              <img :src="require('@/assets/icons/bill-icon.svg')" alt="bill-debt" draggable="false" width="30" />
            </b-avatar>

            <!-- text -->
            <p class="m-0">گزارش تجمیعی واریزی ها</p>
          </div>
        </div>
      </b-card>
    </main>
  </section>
</template>
  
<script>
import {
  BAvatar,
  BCard
} from "bootstrap-vue";
import { getBill } from '/src/api/billServices'
import helper from "@/services/general"

export default {
  name: "ManagementReportIndex",
  mixins: [helper],
  components: {
    BAvatar,
    BCard
  },
  methods: {
    async goReport(type) {
      let message = ""
      switch (type) {
        case 1:
          message = "گزارش بدهی واحدها بر اساس سر فصل"
          break;

        case 2:
          message = "گزارش تجمیعی پرداختی ها"
          break;

        case 3:
          message = "گزارش تجمیعی واریزی ها"
          break;

        default:
          message = ""
      }

      try {
        const { data } = await getBill({ data: { type: type } })

        this.$store.commit("bill/setSuccessObject", {
          pageTitle: "گزارشات مدیریتی",
          pageDec: "لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ و با استفاده از طراحان گرافیک است",
          successMsg: "گزارش با موفقیت ساخته شد",
          link: data.link,
          data: [
            { key: "تاریخ", value: this.jdate() },
            { key: "نوع گزارش", value: message }
          ]
        })
        this.$router.push({ name: "success-page" })
      } catch (error) {
        console.error(error)
      }
    }
  }
}
  </script>
  
<style lang="scss" scoped>
section.management-report {
  main {
    margin-top: 67px;

    .card {
      margin-bottom: 20px !important;
      .card-body {
        padding: 10px !important;
        a {
          color: #5e5873;
          p {
            font-size: 15px;
            margin-left: 9px !important;
          }
        }
      }
    }
  }
}
